import React, { useMemo } from 'react';
import { makeStyles, Theme, Typography, Box } from '@material-ui/core';
import { MENTORING_EXPIRY } from 'src/constants';
import FadeUp from 'components/Animated/FadeUp';
import SvgElements from './SvgElements';
import differenceInCalendarDays from 'date-fns/differenceInCalendarDays';

const useStyles = makeStyles<Theme>(({ typography, breakpoints, spacing, palette }) => ({
  root: {
    height: `100vh`,
    backgroundColor: '#FFFFFF',
    padding: `0 ${spacing(2)}px`,
  },
  contentsArea: {
    zIndex: 1,
  },
  svgArea: {
    zIndex: 0,
  },
  subtitleWrapper: {
    backgroundColor: '#C5E5DD',
    padding: '0 14px',
  },
  subtitle: {
    whiteSpace: 'nowrap',
    fontWeight: typography.fontWeightBold,
    fontSize: 24,
    lineHeight: 1,
    transform: 'translateY(-8px)',
    [breakpoints.down('md')]: {
      fontSize: 16,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    fontSize: 56,
    marginTop: 22,
    lineHeight: '81px',
    '& strong': {
      color: palette.primary.main,
    },
    [breakpoints.down('md')]: {
      fontSize: 24,
      marginTop: 0,
    },
  },
  description: {
    textAlign: 'center',
    marginTop: 32,
    fontSize: 18,
    [breakpoints.down('md')]: {
      fontSize: 14,
      marginTop: 16,
    },
  },
  cta: {
    marginTop: 78,
    fontSize: 24,
    fontWeight: typography.fontWeightBold,
    padding: '8px 50px',
    [breakpoints.down('md')]: {
      fontSize: 16,
      marginTop: 30,
    },
  },
}));

const today = new Date();
const expiryDiff = differenceInCalendarDays(MENTORING_EXPIRY, today);

interface Props {
  onClickApply?: () => void;
}
const IntroSection = ({}: Props) => {
  const styles = useStyles();
  const dday = useMemo(() => {
    if (expiryDiff === 0) {
      return 'D-Day';
    } else if (expiryDiff < -100) {
      return '';
    } else if (expiryDiff < 0) {
      return '신청 마감';
    } else {
      return `D-${expiryDiff}`;
    }
  }, []);

  return (
    <Box className={styles.root} display="flex" justifyContent="center" alignItems="center">
      <Box display="flex" flexDirection="column" alignItems="center" className={styles.contentsArea}>
        {dday === '' ? null : (
          <div className={styles.subtitleWrapper}>
            <FadeUp delay={0}>
              <Typography variant="h1" className={styles.subtitle}>
                루비콘 샌드박스 신청 마감 {dday}
              </Typography>
            </FadeUp>
          </div>
        )}
        <FadeUp delay={0}>
          <Typography variant="h2" className={styles.title}>
            LUBYCON <strong>SANDBOX</strong>
          </Typography>
        </FadeUp>
        <FadeUp delay={0}>
          <Typography className={styles.description}>
            우리가 선망하는 대부분의 일 잘 하는 회사들은 대부분 애자일, 린 방법론 등을 사용하며 최소 비용으로 제품을
            릴리즈하고
            <br />
            사용자의 행동 데이터를 기반으로 가설을 검증하는 이터레이션을 반복하고 있어요.
          </Typography>
        </FadeUp>
        <FadeUp delay={200}>
          <Typography className={styles.description}>
            그래서 개발자나 디자이너들도 이제 더 이상 코딩이나 디자인만 잘 하는 것이 아니라,
            <br />
            비즈니스에 대한 관심을 가지고 임팩트를 만들어 낼 수 있는 사람이 인정받는 시대가 된 것이죠.
          </Typography>
        </FadeUp>
        <FadeUp delay={400}>
          <Typography className={styles.description}>
            이런 지식들은 보통 일 잘하는 회사에 들어가면 자연스럽게 배운다고들 하던데...
            <br />
            그런 회사에 다니지 않고 있는 나는 도대체 어디서 이런 것들을 배워야 하는 걸까요?
          </Typography>
        </FadeUp>
      </Box>
      <div className={styles.svgArea}>
        <SvgElements />
      </div>
    </Box>
  );
};

export default IntroSection;
